.banner-cont-desk {
  height: 15vh;
  background-size: cover;
  width: 100%;
  margin: 0% 0% 1% 0% !important;
  background-color: #000000;
}

.banner-img-desk {
  height: 15vh;
  width: 100%;
  opacity: 0.7;
  margin: 0% 0% 1% 0% !important;
  background-color: transparent;
}

.portal-desk {
  display: inline-block;
  font-size: 2vh;
  margin: 0% 0% 0% 1%;
  color: #000000;
  transition: background-color 0.3s;
  padding: 0% 0.5%;
}
.portal-desk:hover {
  background-color: #c7ebf8;
}


.banner-cart-btn-desk {
  width: 16vh;
  height: 15vh;
  background-color: #ffffff6f;
  margin: 0% 1%;
  cursor: pointer;
  transition: background-color 0.3s;
}

.banner-cart-btn-desk:hover {
  background-color: rgb(0, 204, 255);
  border: 2px solid #0000004c;
}