.pre-prod-cont-desk {
}

.pre-prod-cont-desk-list-f-desk {
  margin: 0% 0% 0% 0%;
}

.pre-prod-cont-list-nf-desk {
  margin: 0% 0% 0% 14%;
}
.pre-card-cont-desk {
  cursor: pointer;
  margin: 3% 0% 0% 1.5%;
  padding: 0.3vh 0.3vw 0vh 0.3vw;
  text-align: center;
  transition: background-color 0.3s;
  max-width: 40vh;
  word-wrap: break-word;
}
.pre-card-img-desk {
  width: 40vh;
  height: 40vh;
}

.pre-card-txt-desk {
  font-size: 2vh;
}
.pre-card-btn-cont-desk {
}
.pre-card-inp-desk {
  font-size: 3vh;
  width: 25%;
}

.pre-card-ammount-btn-desk {
  margin: 2% 5%;

  border: none;
  background-size: calc(100% - 10%);
  background-position: center;
  background-repeat: no-repeat;
  width: 5vh;
  height: 5vh;
  cursor: pointer;
  border-radius: 50%;
}

.pre-card-fav-desk:hover {
  background-color: #ff7c7c;
}

.pre-card-buy-desk:hover {
  background-color: #8aa9ef;
  border-radius: 20%;
}

.pre-card-go-desk:hover {
  background-color: #8aa9ef;
}
