.main-shop-btn {
  font-family: "Poppins", sans-serif;
  background-image: url("https://madera-sostenible.com/wp-content/uploads/2020/05/AHEC_Madera-aserrada-de-roble-rojo-980x600.jpg");
  background-size: cover;
  background-position: center;
  text-shadow: 2px 2px 2px rgba(206, 89, 55, 0), 2px 4px 5px #000000;
  color: #b4e8fb;
  border-radius: 0.5%;
}

.main-shop-btn-mob {
  font-size: 3vh;
  height: 0vh;
  padding: 10vh 0vh 5vh 1vh;
  text-align: left;
  margin: 4% 0%;
  

}

.main-shop-btn-desk {
  font-size: 4.5vh;
  padding: 4vh 5%;
  margin: 4% 5%;
  color: #c7ebf8;
}



.main-wp-btn{
    position: fixed;
    right: 1%;
    bottom: 0%;
    width: 10vh;
}


