.mod-cart-cont-desk {
  width: 70%;
  top: 10%;
  left: 10%;
  border-radius: 2%;
}
.mod-cart-clbtn-desk {
  width: 6vh;
  height: 6vh;
  cursor: pointer;
  background-color: transparent;
  transition: background-color 0.35s;
}

.mod-cart-clbtn-desk:hover {
  background-color: #ff0000;
}

.mod-cart-info-desk {
}
.mod-cart-info-cont-desk {
  display: inline-block;
  margin: 0% 3%;
}
.mod-cart-info-buy-desk {
  padding: 0% 20% !important;

  background-color: #66a81b;
  border: 1px solid #000000;
  cursor: pointer;
  transition: background-color 0.3s, border 0.3s;
}
.mod-cart-info-buy-desk:hover {
  background-color: #d0faa0;
  border: 1px solid #b1fa5e;
}
.mod-cart-info-money-desk {
}
.mod-cart-info-clear-desk {
  font-size: 2.5vh;
  border-radius: 1%;
  background-color: #c18f8f;
  border: 1px solid #000000;
  cursor: pointer;
  transition: background-color 0.3s, border 0.3s;
}
.mod-cart-info-clear-desk:hover {
  background-color: #ff0000;
  border: 1px solid #ff0000;
}
