@import url("https://fonts.googleapis.com/css2?family=Poppins&display=swap");

/* 

font-family: 'Poppins', sans-serif;

*/
* {
  padding: 0;
  margin: 0;
}
.banner-cont {
  display: inline-block;
}

.banner-img {
  background-image: url("https://maderera-andina.com/wp-content/uploads/2020/10/maderera-andina-la-madera-y-la-exportacion.jpg");
  background-position: center;
  display: inline-block;
}

.portal {
  font-family: "Poppins", sans-serif;
}

.banner-cart-btn {
  display: inline-block;
  background-image: url("https://cdn.icon-icons.com/icons2/3553/PNG/512/online_purchase_trolley_shopping_cart_ecommerce_icon_224940.png");
  background-position: center;
  background-repeat: no-repeat;
  border-radius: 50%;
  border: none;
  background-size: 70% 80%;
}



