
/* FOCUS */

.cat-list-cont-focus-desk {
  text-align: left;
  border: 1px solid #000000;
  padding: 0vh 1vw 1vh 0vw;
  height: 74%;
  bottom: 1%;
}

.cat-list-content-focus-desk {
}
.cat-list-link-focus-desk {
  display: inline-block;
  font-size: 2.5vh;
  margin: 1vh 0vw 0vh 0vw;
  padding: 1vh 2vw 1vh 2vw;
  color: #000000;
  width: calc(100% - 3vw);  
  transition: background-color 0.5s;
}

.cat-list-link-focus-desk:hover {
  background-color: #c7ebf8;
}

.cat-list-btn-focus-desk {
  background-color: #ffffff;
  width: calc(100% + 1vw);
  height: 5vh;
  font-size: 2vh;
  border: none;
  cursor: pointer;
  transition: background-color 0.3s;
}

.cat-list-btn-focus-desk:hover {
  background-color: #c7ebf8;
}


