/* FOCUS */

.cat-list-cont-focus-mob {
  text-align: left;
  border: 1px solid #000000;
  padding: 0vh 1vw 1vh 0vw;
  height: 70vh;
}

.cat-list-content-focus-desk {
}

.cat-list-link-focus-mob {
  display: inline-block;
  font-size: 2.5vh;
  margin: 1vh 0vw 0vh 0vw;
  padding: 1vh 2vw 1vh 2vw;
  color: #000000;
  width: calc(100% - 3vw);
  transition: background-color 0.5s;
}

.cat-list-btn-focus-mob {
  background-color: #ffffff;
  width: calc(100% + 1vw);
  height: 5vh;
  font-size: 2vh;
  border: none;
  cursor: pointer;
  transition: background-color 0.3s;
}

/* NO FOCUS */

.cat-list-cont-nofocus-mob {
  position: fixed !important;
  text-align: left;
  border: 1px solid #000000;
  padding: 0vh 1vw 1vh 0vw;
  height: 70vh;
}

.cat-list-content-nofocus-desk {
}


.cat-list-btn-nofocus-mob {
  background-color: #ffffff;
      width: calc(100% + 1vw);
      height: 70vh;
      font-size: 2vh;
      border: none;
      cursor: pointer;
      transition: background-color 0.3s;
}
