.pre-prod-cont {
  transition: margin 0.3s;
}

.pre-prod-cont-list-f {
  margin: 0% 0% 0% 3%;
}

.pre-prod-cont-list-nf {
  margin: 0% 0% 0% 14%;
}

.pre-card-cont {
  display: inline-flex;

  border: 2px solid #000000;
}

.pre-card-img {
}

.pre-card-txt {
  font-family: "Poppins", sans-serif;

  color: #000000;
}

.pre-card-btn-cont {
  display: inline-block;
  width: 100%;
}

.pre-card-inp {
}

.pre-card-buy {
  background-image: url("https://cdn.icon-icons.com/icons2/3553/PNG/512/online_purchase_trolley_shopping_cart_ecommerce_icon_224940.png");
  background-color: transparent;
}

.pre-card-fav {
  background-image: url("https://cdn.icon-icons.com/icons2/38/PNG/512/like_favorite_heart_5759.png");
  background-color: transparent;
}

.pre-card-go {
  background-image: url("https://cdn.icon-icons.com/icons2/1946/PNG/512/1904671-arrow-arrow-right-change-direction-next-page-right_122521.png");
  background-color: transparent;
}

.ammount-fav-svg {
  cursor: pointer;
}

.pre-calcu {
  font-family: "Poppins", sans-serif;
