@import url("https://fonts.googleapis.com/css2?family=Poppins&display=swap");

/* 

font-family: 'Poppins', sans-serif;

*/

a {
  text-decoration: none;
}

ul {
  list-style-type: none;
}

/*  FOCUS */


.cat-list-cont-focus {
  position: fixed;
  transition: padding 0.3s, width 0.2s;
  z-index: 10;
  background-color: #ffffff;
}

.cat-list-content-focus {
  transition: opacity 0.3s;
}

.cat-list-link-focus {
  font-family: "Poppins", sans-serif;
}

.cat-list-btn-focus {
  font-family: "Poppins", sans-serif;
}

/* NO FOCUS */

.cat-list-cont-nofocus {
  position: fixed;
  transition: padding 0.3s, width 0.2s;
}

.cat-list-content-nofocus {
  transition: opacity 0.3s;
}

.cat-list-link-nofocus {
  font-family: "Poppins", sans-serif;
}

.cat-list-btn-nofocus {
  font-family: "Poppins", sans-serif;
}
