@import url("https://fonts.googleapis.com/css2?family=Poppins&display=swap");

/* 

font-family: 'Poppins', sans-serif;

*/

.mod-cart-cont {
  position: fixed;
  background-color: #ffffff;
  height: fit-content;
  border: 3px solid #000000;
}

.mod-cart-clbtn {
  background-image: url("https://cdn.icon-icons.com/icons2/2719/PNG/512/x_circle_icon_174835.png");

  border-radius: 50%;
  border: none;
  display: inline-block;
  margin: 1%;
  background-size: cover;
  background-position: center;
}

.mod-cart-info {
  background-color: #b1e9fa82;
  border: 2px solid #00000056;
}

.mod-cart-info-cont {
}

.mod-cart-info-buy {
  font-family: "Poppins", sans-serif;
  font-size: 2.5vh;
  border-radius: 1%;
}

.mod-cart-info-money {
  font-family: "Poppins", sans-serif;
  font-size: 2.5vh;
}

.mod-cart-info-clear {
  font-family: "Poppins", sans-serif;
  font-size: 2.5vh;
  border-radius: 1%;
}

.mod-cart-nopro {
  font-family: "Poppins", sans-serif;
  text-align: center;
}

.mod-cart-nopro-desk {
  font-size: 4vh;
  padding: 0% 0% 5% 0%;
}
.mod-cart-text {
}
.mod-cart-link {
  color: #0eb7ff;
  text-shadow: 1px 1px 1px rgba(206, 89, 55, 0), 1px 1px 1px #000000,
    1px 1px 1px rgba(206, 89, 55, 0), 1px 1px 1px #000000,1px 1px 1px rgba(206, 89, 55, 0), 1px 1px 1px #000000,
    1px 1px 1px rgba(206, 89, 55, 0), 1px 1px 1px #000000;
}

.mod-cart-nopro-mob {
  font-size: 3vh;
  padding: 0% 0% 10% 0%;
}
